@import url('https://fonts.googleapis.com/css?family=Josefin+Sans');

body   {
    padding: 50px 200px;
    text-align: center;
    background-color: rgb(227, 227, 227);
}

input   {
    background-color: white;
    border: 1px solid rgb(174, 174, 174);
    border-radius: 10px;
    padding: 5px 10px;
    margin: 5px;
}

button{
    color: white;
    background-color: blue;
    font-weight: 600;
    padding: 10px 15px;
    border-radius: 10px;
    border: none;
    margin: 10px;
}



.message  {
    background-color: rgb(33, 186, 6);
     display: inline-flex;
    padding: 10px 100px;
    color: white;
    transition: ease-in 0.3s;
}

.process  {
    background-color:yellow;
     display: inline-flex;
    padding: 10px 100px;
    color: black;
    transition: ease-in 0.3s;
}

.alert  {
    display: inline-flex;
    background-color: red;
    padding: 10px 100px;
    color: white;
    transition: ease-in 0.3s;
}

.hidden {
    visibility: collapse;
    transform: translateY(-20px);
}

.show   {
    visibility: show;
    transform: translateY(0px);

}

.delete {
    background-color: red;
    color: white;
    padding: 5px 10px;
}

.delete:hover   {
    background-color: rgb(174, 0, 0);
    color: white;
    padding: 5px 15px;
    transition: ease-in-out 0.2s;
}

.delete:active   {
    background-color: rgb(0, 0, 0);
    color: white;
    border: black solid 1px;
    padding: 5px 15px;
    transition: ease-in-out 0.2s;
}

.phonebook  {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: bold;
}

